import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductAttributesComponent } from './product-attributes.component';
import { AddProductAttributeComponent } from './add-product-attribute/add-product-attribute.component';
import { EditProductAttributeComponent } from './edit-product-attribute/edit-product-attribute.component';

const routes: Routes = [
  { path: '', component: ProductAttributesComponent },
  { path: 'add', component: AddProductAttributeComponent },
  { path: 'edit/:id', component: EditProductAttributeComponent },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProductAttributesRoutingModule {}
