import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators, FormArray } from '@angular/forms';
import { ProductAttributesService } from '../product-attributes.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-product-attribute',
  templateUrl: './edit-product-attribute.component.html',
  styleUrls: ['./edit-product-attribute.component.scss'],
})
export class EditProductAttributeComponent implements OnInit, AfterViewInit {
  image;
  productAttributeId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private productAttributesService: ProductAttributesService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    name: this.fb.group({
      en: ['', Validators.required],
      ar: [''],
    }),
    values: this.fb.array([]),
  });

  ngOnInit(): void {
    this.productAttributeId = this.route.snapshot.paramMap.get('id');
  }
  ngAfterViewInit() {
    this.getOne(this.productAttributeId);
  }

  getOne(id) {
    this.productAttributesService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];
        this.form.get('name.en').patchValue(res['data']['name']['en']);
        this.form.get('name.ar').patchValue(res['data']['name']['ar']);
        console.log(res['data']['values']);
        const values = this.form.get('values') as FormArray;

        for (let i = 0; i < res['data']['values'].length; i++) {
          values.push(
            this.fb.group({
              en: ['', Validators.required],
              ar: ['', Validators.required],
            })
          );
          values.controls[i]
            .get('en')
            .patchValue(res['data']['values'][i]['en']);
          values.controls[i]
            .get('ar')
            .patchValue(res['data']['values'][i]['ar']);
        }
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  addValue() {
    const values = this.form.get('values') as FormArray;
    values.push(
      this.fb.group({
        en: ['', Validators.required],
        ar: ['', Validators.required],
      })
    );
  }

  removeValue(valueIndex) {
    const values = this.form.get('values') as FormArray;
    if (values.controls.length > 0) {
      values.removeAt(valueIndex);
    }
  }

  onSubmit(formValue) {
    this.productAttributesService
      .updateOne(formValue, this.productAttributeId)
      .subscribe(
        (res) => {
          console.log(res);
          this.image = null;
          this.form.reset();
          this.toastr.info(res['message'], 'Info');
          this.router.navigate(['/product-attributes']);
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
  }
}
